"use client";
import { useRouter } from "next/router";
import { useState } from "react";

export default function Button({title }) {}
    const router = useRouter();
    const [isLoading, setIsLoading] = useState(false);
    
    const handleClick = () => {
        setIsLoading(true);
        // Simulate a network request
        setTimeout(() => {
        setIsLoading(false);
        router.push("/jobs");
        }, 1000);
    };
    
    return (
        <button
        onClick={handleClick}
        className="bg-emerald-700 text-white px-4 py-2 rounded hover:bg-emerald-800 transition-colors"
        disabled={isLoading}
        >
        {isLoading ? "Loading..." : title}
        </button>
    );
    